.inputLabel{
  display: inline-block;
  width: 275px;
    font-size: 24px;
}
.inputText{ 
    width: 300px;
    font-size: 24px;
}
.space{
  margin: 15px;
}
.button{
  padding-left: 25px;
    padding-right: 25px;
    padding-top: 6px;
    padding-bottom: 6px;
    font-size: 24px;
    color: white;
    background-color: black;
}
.wrapper{
  height: 100vh;
    box-sizing: border-box;
}
.header{
    height: 10%;
    border: 2px solid red;
    box-sizing: border-box;
}
.container{
  height: 90%;
    border: 2px solid red;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
}
.sidebar{
  width: 20%;
    height: 100%;
    border: 2px solid red;
    box-sizing: border-box;
}
.main-content{
  width: 80%;
    height: 100%;
    border: 2px solid red;
    box-sizing: border-box;
}
.table-border{
  border: 2px solid black;
  border-collapse: collapse;
  text-align: center;
}
.table-width{
  width: 100%;
}
.profile-pic{
  border-radius: 60px;
    width: 60px;
}
